.about-us {
  max-width: 375px;
  width: 100%;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
}

.about-us .title {
  color: #1e2827;
  white-space: nowrap;
  letter-spacing: -.64px;
  margin: 0 auto;
  padding-bottom: 40px;
  font-family: Epilogue, sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 40px;
}

.text-section {
  color: #141414;
  letter-spacing: -.28px;
  flex-direction: column;
  gap: 20px;
  margin: 0 auto;
  padding: 0;
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 125%;
