    align-items: center;
    padding: 64px 20px 128px;
    display: flex;
  }

  .content-section {
    flex-direction: row;
    justify-content: center;
    margin: 0;
    display: flex;
  }

  .content-section .title {
    margin-right: 60px;
    font-size: 40px;
  }

  .content-section .text-section {
    flex-direction: row;
    margin: 0 auto;
    display: flex;
  }

  .about-us-gif {
    height: 465px;
    margin: 0 auto;
  }
}

